import React from "react";
import HeadingTab from "../components/HeadingTab/HeadingTab";
import { Grid, GridItem, Stack } from "@chakra-ui/react";
import Location from "../components/Location";

import Contact from "../components/Contact";
import { contacts } from "../data/data";

function Contacts () {
  return (
    <Stack>
      <HeadingTab text="" />
      <Grid px={8} templateColumns='repeat(2, 1fr)'>
        <GridItem colSpan={{base: 2, xl: 1}} pr={{base: 0, xl: 10}}>
          <Stack spacing={{base: 10, xl: 10}}>
            {
              contacts.people.map((contact) => (
                <Contact
                fio={contact.fio}
                phone={contact.phone}
                email={contact.email}
              />
              ))            
            }
          </Stack>
        </GridItem>

        <GridItem colSpan={{base: 2, xl: 1}} pt={{base: 10, xl: 0}}>
          <Location />
        </GridItem>
      </Grid>
    </Stack>
  )
}

export default Contacts