import React from "react";
import { contacts } from "../data/data";

function YaMapsWidget () {
  return (
    <div style={{ position: "relative", overflow: "hidden", height: '100%', width: '100%' }} >
      <a
        href={contacts.location.yaMaps.cityLink}
        style={{ color: "#eee", fontSize: 12, position: "absolute", top: 0 }}
      >
        {contacts.location.yaMaps.cityName}
      </a>
      <a
        href={contacts.location.yaMaps.streetLink}
        style={{ color: "#eee", fontSize: 12, position: "absolute", top: 14 }}
      >
        {contacts.location.yaMaps.streetName}
      </a>
      <iframe
        src={contacts.location.yaMaps.widgetLink}
        width={'100%'}
        height={'100%'}
        frameBorder={1}
        allowFullScreen={true}
        style={{ position: "relative" }}
      />
    </div>
  )
}

export default YaMapsWidget