import { Flex, Stack, Image, Text, Box, Icon } from '@chakra-ui/react';
import React from 'react';
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';

import { MdArrowBack, MdArrowForward } from 'react-icons/md';

import { ourClientsCards } from '../../data/data';

const buttonStyle = {
    background: 'none',
    border: '0px',
    padding: '10px'
};

const properties = {
    prevArrow: <button style={{ ...buttonStyle }}><Icon as={MdArrowBack} boxSize={8} color={'white'} /></button>,
    nextArrow: <button style={{ ...buttonStyle }}><Icon as={MdArrowForward} boxSize={8} color={'white'} /></button>,
    transitionDuration: 500,
    easing: 'cubic-out'
}

function ImageGallery () {
  return (
    <Box 
      borderRadius='3xl' 
      style={{
        overflow: 'hidden'
      }}
    >
      <Slide {...properties}>
      {ourClientsCards.map((card, index) => (
        <Flex
          height={'100%'}
          minHeight={'80vh'}
          backgroundImage={card.background}
          backgroundSize={'cover'}
          backgroundRepeat={'no-repeat'}
          backgroundPosition={'center center'}
        >
          
            <Stack   
              key={index}          
              w={'full'}
              height={'full'}
              p={8}
              align={'center'}
              justify={'center'}
            >
              <Image 
                src={card.logo} 
                boxSize={{base: '200px', xl: '300px'}}
                m={4}
              />
              <Text 
                color={'white'} 
                maxWidth={'lg'} 
                textAlign={'center'} 
                fontWeight={'medium'}
                fontSize={{base: 'lg', xl: '2xl'}}
              >
                {card.text}
              </Text>
            </Stack>
        </Flex>
        ))
      }
      </Slide>
    </Box>
  );
};

export default ImageGallery