import { Heading, Icon, List, ListIcon, ListItem, Stack, Text } from "@chakra-ui/react";
import React from "react";
import { ParagraphProps } from "../types";

function Paragraph ({title, text, uppercase=false, marker=false, markerIcon=null}: ParagraphProps) {
  return (
    <Stack spacing={6}>
      <Heading fontWeight={'extrabold'} textTransform='uppercase'>
        {title}
      </Heading>

      <List size='xl' spacing={4}>
        {
          text.map((string) => (
            <ListItem textTransform={uppercase ? 'uppercase' : 'none'}>
              {
                (marker && markerIcon) ? <ListIcon as={markerIcon} /> : null
              }
              {string}
            </ListItem>
          ))
        }
      </List>
    </Stack>
  )
}

export default Paragraph