import { Stack, Text, Heading, Link } from "@chakra-ui/react";
import React from "react";
import { ContactProps } from "../types";

function Contact ({fio, phone, email}: ContactProps) {
  return (
    <Stack
      border={'1px solid black'}
      borderRadius={'3xl'}
      height={'100%'}
      p={8}
    >
      <Heading size={'md'} textTransform={'uppercase'} pb={4}>
        {fio}
      </Heading>
      <Text as={Link} href={`tel:${phone}`} isExternal target="_blank">
        {phone}
      </Text>
      <Text as={Link} href={`mailto:${email}`} isExternal target="_blank">
        {email}
      </Text>
    </Stack>
  )
}

export default Contact